.modalBackground {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.7);
    z-index: 1000;
  }
  
  .modalOverlay {
    position: absolute;
    width: 100%;
    height: 100%;
    justify-content: right;
  }
  
  .modalContainer {
    background: var(--theme-color);
    width: 100%;
    height: 100%;
    padding: 20px;
    border-radius: 5px;
    position: relative;
    display: flex;
    flex-direction: column;
    /* justify-content: center;
    align-items: center; */
    z-index: 1001;
  
  
  }
  .closeButton {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
  }

  @media screen and (min-width: 768px ) and (max-width: 1157px)
  {
    .modalContainer {
      width: 50%;
    }

    .modalBackground {

      justify-content: flex-end;
    }
  }

  @media screen and (min-width: 1158px)
  {
    .modalBackground {

      display: none;
    }
  }
